/* Container */
.App {
  display: flex;
  justify-content: space-between;
  padding: 20px;
}

/* Column */
.column {
  flex: 1;
  background-color: #f0f0f0;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
}

/* Headers */
.header {
  font-size: 24px;
  margin-bottom: 20px;
}

.header-board {
  font-size: 20px;
  margin-bottom: 10px;
}

/* Cards */
.cards {
  list-style: none;
  padding: 0;
}

.card {
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
}

.card h3 {
  font-size: 18px;
  margin: 0;
  margin-bottom: 5px;
}

.card p {
  font-size: 14px;
  color: #555;
  margin-bottom: 10px;
}

/* Edit Mode */
.editing {
  background-color: #f5f5f5;
}

/* Input Fields */
input[type="text"] {
  width: 96%;
  padding: 5px;
  margin-bottom: 5px;
  border: 1px solid #ccc;
  border-radius: 3px;
}

/* Buttons */
button {
  cursor: pointer;
  padding: 5px 10px;
  margin-top: 5px;
  border: none;
  border-radius: 3px;
}

/* Add Task Button */
.card-input button {
  background-color: #007bff;
  color: #fff;
}

.card-input button:hover {
  background-color: #0056b3;
}

/* Edit Button */
.edit-button {
  background-color: #28a745;
  color: #fff;
}

.edit-button:hover {
  background-color: #1e7e34;
}

/* Delete Button */
.delete-button {
  background-color: #dc3545;
  color: #fff;
}

.delete-button:hover {
  background-color: #bd2130;
}
